<template>
  <div>
    <LayoutBox one="平台活动">
      <div class="tool_group">
        <div class="btn_group">
          <router-link to="/adminAddActivity"><el-button type="success" size="small" icon="el-icon-plus" class="inquire" round>新增活动</el-button></router-link>
          <!-- <el-button type="danger" size="small" icon="el-icon-delete" round>批量删除</el-button> 后端没有接口 -->
        </div>
        <el-form class="input_group" :model="form" :inline="true" size="mini">
          <el-form-item label="分类">
            <el-select v-model="form.activeType" placeholder="请选择">
              <el-option label="秒杀" :value="1"></el-option>
              <el-option label="团购" :value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="时间筛选 ">
            <el-date-picker v-model="form.startTime" type="date" placeholder="开始日期"></el-date-picker>至
            <el-date-picker v-model="form.endTime" type="date" placeholder="结束日期"></el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" style="width: 80px" @click="onSearch">查询</el-button>
          </el-form-item>
        </el-form>
      </div>

      <el-table :data="activityList" style="width: 100%">
        <el-table-column type="selection" width="55" align="center" />
        <el-table-column prop="activeName" label="名称" align="center" show-overflow-tooltip />
        <el-table-column label="分类" align="center">
          <template slot-scope="scope">
            <p>{{ scope.row.activeType == 0 ? '拼团' : '秒杀' }}</p>
          </template>
        </el-table-column>
        <el-table-column label="活动时间" align="center" width="350" show-overflow-tooltip>
          <template slot-scope="scope">
            <p>{{ scope.row.startTime }} 至 {{ scope.row.endTime }}</p>
          </template>
        </el-table-column>
        <el-table-column prop="applyNum" label="报名人数" align="center" />
        <el-table-column prop="date" label="活动状态" align="center">
          <template slot-scope="scope">
            <span v-if="new Date().getTime() < scope.row.startTimeDate">未开始</span>
            <span v-else-if="new Date().getTime() > scope.row.startTimeDate && new Date().getTime() < scope.row.endTimeDate">进行中</span>
            <span v-else-if="new Date().getTime() > scope.row.endTimeDate">已结束</span>
          </template>
        </el-table-column>
        <el-table-column prop="date" label="状态" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.state === 0">暂停</span>
            <span v-else-if="scope.row.state === 1">正常</span>
            <!-- <span v-if="new Date().getTime() < scope.row.startTimeDate">未开始</span>
            <span v-else-if="new Date().getTime() > scope.row.startTimeDate && new Date().getTime() < scope.row.endTimeDate">进行中</span>
            <span v-else-if="new Date().getTime() > scope.row.endTimeDate">已结束</span> -->
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center" width="130">
          <template slot-scope="scope">
            <el-button type="text" @click="onUpdate(scope)">编辑</el-button>
            <el-button v-if="scope.row.state === 1" style="color: #fd9a30" type="text" @click="onEnd(scope)">下架</el-button>
            <el-button v-else-if="scope.row.state === 0" style="color: #fd9a30" type="text" @click="onStart(scope)">上架</el-button>
            <el-button type="text" @click="onDelete(scope)">删除</el-button>
          </template>
        </el-table-column>
        <!-- <el-table-column label="详情" align="center">
          <template>
            <i class="el-icon-more" />
          </template>
        </el-table-column> -->
      </el-table>
      <!-- 分页 -->
      <el-pagination background layout="prev, pager, next" :total="total" @current-change="handleCurrentChange" />
    </LayoutBox>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'

export default {
  name: 'AdminActivityList',

  components: {
    LayoutBox
  },

  data() {
    return {
      form: {}, // 查询
      addTime: '', // 添加时间选择
      total: 0, // 秒杀分页总条数
      pageNo: 1, // 现在是第几页
      activityList: [] // 所有的活动的列表包括秒杀团购
    }
  },

  created() {
    this.apiGetActivityList()
  },

  methods: {
    // 分页
    handleCurrentChange(pageNo) {
      this.pageNo = pageNo
      this.apiGetActivityList()
    },
    // 删除
    onDelete (scope) {
      this.apiActivityDelete(scope)
    },
    // 终止
    onEnd(scope) {
      this.apiActivityUpOrDown(scope.row.id, 0)
        .then((res) => {
          this.$message.success('已终止活动')
          scope.row.state = 0
        })
    },
    // 启用
    onStart(scope) {
      this.apiActivityUpOrDown(scope.row.id, 1)
        .then((res) => {
          this.$message.success('已启用活动')
          scope.row.state = 1
        })
    },
    // 查询
    onSearch () {
      this.apiGetActivityList()
    },
    // 编辑
    onUpdate (scope) {
      this.$router.push({ path: '/adminAddActivity', query: { id: scope.row.id }})
    },

    // api接口----------------------------------------------------
    // 获取活动列表
    apiGetActivityList() {
      let data = {
        pageNo: this.pageNo
      }
      Object.assign(data, this.form) // 模糊查询
      this.$http.post('/woho/activity/manage/query', data)
        .then((res) => {
          this.activityList = res.data.data.list
          this.total = res.data.data.total
          // 把时间转换成时间戳再在对象里面添加进去
          this.activityList.map((item) => {
            item.startTimeDate = new Date(item.startTime.replace(/-/g, '/')).getTime()
            item.endTimeDate = new Date(item.endTime.replace(/-/g, '/')).getTime()
          })
        })
    },
    // 删除
    apiActivityDelete (scope) {
      this.$http.delete(`/woho/activity/manage/delete/${scope.row.id}`)
        .then((res) => {
          if (res.data.success) {
            this.$message.success('删除成功')
            this.activityList.splice(scope.$index, 1)
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 0-上架 1-下架
    apiActivityUpOrDown(id, state) {
      return this.$http.post(`/woho/activity/manage/upOrDown/${id}/${state}`)
        .then((res) => {
          if (res.data.success) {
            return
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
  }
}
</script>

<style lang="scss" scoped>
    .tool_group{
      display: flex;
    }
    .input_group{
      margin-left: 30px;
    }
</style>
